<template>
  <div class="singer" ref="singer">
    <listview :data='singerList' @selectItem = 'selectSinger'></listview>
    <router-view></router-view>
  </div>
</template>

<script>
import { getSingerList } from "api/singer";
import { ERR_OK } from "api/config";
import Singer from "common/js/singer";
import Listview from "base/listview/listview";
// import {testMixin} from 'common/js/mixin'
import {mapMutations} from 'vuex'


const HOT_NAME = "";
const HOT_SINGER_LEN = 10;
const OTHER_NAME= '';

export default {
  data() {
    return {
      singerList: []
    };
  },
  // mixins:[testMixin],
  created() {
    this._getSingerList();
  },
  methods: {
    ...mapMutations({
      setSinger:'SET_SINGER'
    }),
    selectSinger(singer){
      this.$router.push(`singer/${singer.id}`)
      this.setSinger(singer);
    },
    _getSingerList() {
      getSingerList().then(res => {
        if (res.code === ERR_OK) {
          this.singerList =this. __normalizeSinger(res.data.list)
          // console.log(this.__normalizeSinger(res.data.list));
        }
      });
    },
    // 
    __normalizeSinger(list) {
      // map
      let map = {
        hot: {
          title: HOT_NAME,
          items: []
        }
      };

      list.forEach((item, index) => {
        if (index < HOT_SINGER_LEN) {
          map.hot.items.push(
            new Singer({
              id: item.Fsinger_mid,
              name: item.Fsinger_name
            })
          );
        }

        let key = item.Findex;
        if (!key.match(/[a-zA-Z]/)) {
          key = OTHER_NAME;
        }        
        // mapmap[key],
        if (!map[key]) {
          map[key] = {
            title: key,
            items: []
          };
        }

        map[key].items.push(
          new Singer({
            id: item.Fsinger_mid,
            name: item.Fsinger_name
          })
        );
      });
      //  map
      let ret = [];
      let hot = [];
      let other = [];//title
      for (const key in map) {
        let val = map[key];
        if (val.title === HOT_NAME) {
          hot.push(val);
        } else if (val.title.match(/[a-zA-Z]/)) {
          ret.push(val);
        }else{
          other.push(val);
        }
      }

      //  a-z  A-Z 
      ret.sort((a, b) => {
        return a.title.charCodeAt(0) - b.title.charCodeAt(0);
      });
      // 
      return hot.concat(ret,other);
    }
  },
  components:{
    Listview
  }
};
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
.singer {
  position: fixed;
  top: 88px;
  bottom: 0;
  width: 100%;
  .social-share{
    position fixed
    width 100%
    bottom 0
    left 0
  }
}
</style>



